body {
  overflow-x: hidden;
  background-color: #d4cfcf;
}
body .App .navbar {
  width: 95%;
  padding-right: 5%;
  height: 10vh;
  background-color: #000000;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 26px;
}
body .App .main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}
body .App .main .calc {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .App .main .calc form {
  display: flex;
  flex-direction: column;
}
body .App .main .calc form #result_gasoline {
  display: flex;
  flex-direction: column;
}
body .App .main .menu {
  position: absolute;
  right: 0;
  top: 10vh;
  height: auto;
  bottom: 10vh;
  background-color: white;
  width: 20px;
  padding: 10px;
}
body .App .main .menu #instruction {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}
body .App .main .menu #instruction div {
  cursor: pointer;
}
body .App .main .menu .calculator {
  display: none;
  width: 100%;
  border-bottom: 2px solid black;
  font-size: 20px;
  cursor: pointer;
}
body .App .main .menu .calculator:hover {
  color: #4c80b1;
}
body .App .main .menu #prev {
  cursor: pointer;
  display: none;
  margin-top: 30vh;
  width: 100%;
  justify-content: center;
}
body .App footer {
  position: absolute;
  bottom: 0;
  height: 10vh;
  background-color: black;
  color: white;
  width: 95%;
  padding-right: 5%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 26px;
}
